.input {
	background-color: #fff;
	box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.37);
	padding: 12px;
	margin-right: 5px;
	border: 1px solid #eee;
	border-radius: 2px;
	min-width: 250px;
	font-family: 'Open Sans', sans-serif;
	font-size: 15px;
}

.input::placeholder {
	color: #bbb;
	font-family: 'Open Sans', sans-serif;
	font-size: 15px;
}

.inputContainer {
	display: flex;
	flex-direction: row;
	align-items: center;
	margin-bottom: 16px;
}

.invalid {
	color: #ff0000;
}

@media only screen and (max-width: 650px) {
	.inputContainer {
		flex-wrap: wrap;
		width: 100%;
		flex-direction: column;
	}
}

